"use client";
import React, { useState, useEffect, useRef, SyntheticEvent } from 'react';
import { useLenis } from 'lenis/react';

// --- Data for each slide ---
const slidesData = [
  {
    title: "Generate Code with AI",
    description: "Describe your logic in plain English and watch as the AI generates clean, efficient code in seconds. From Python scripts to complex algorithms.",
    image: "https://images.unsplash.com/photo-1564865878688-9a244444042a?q=80&w=2070&auto=format&fit=crop",
    bgColor: "#F5E7C6",
    textColor: "#000000",
  },
  {
    title: "Debug and Refactor Smarter",
    description: "Paste your buggy code and let the AI identify errors, suggest improvements, and refactor for better readability and performance.",
    image: "https://images.unsplash.com/photo-1516321497487-e288fb19713f?q=80&w=2070&auto=format&fit=crop",
    bgColor: "#F5E7C6",
    textColor: "#000000",
  },
  {
    title: "Learn New Languages, Instantly",
    description: "Translate code snippets between languages. Understand syntax and paradigms of a new language by seeing familiar code transformed.",
    image: "https://images.unsplash.com/photo-1608306448197-e83633f1261c?q=80&w=1974&auto=format&fit=crop",
    bgColor: "#F5E7C6",
    textColor: "#000000",
  },
  {
    title: "Automate Your Workflow",
    description: "From writing documentation to generating unit tests, let AI handle the repetitive tasks so you can focus on building great things.",
    image: "https://images.unsplash.com/photo-1522071820081-009f0129c71c?q=80&w=2070&auto=format&fit=crop",
    bgColor: "#F5E7C6",
    textColor: "#000000",
  },
];

// --- Main App Component ---
const ScrollingFeatureShowcase = () => {
  // State to track the currently active slide index
  const [activeIndex, setActiveIndex] = useState(0);
  // State to track if section is fully in viewport (locked to top)
  const [isFullyVisible, setIsFullyVisible] = useState(false);
  // State to track if internal scroll has completed (at the end)
  const [hasCompletedInternalScroll, setHasCompletedInternalScroll] = useState(false);
  // State to track if scroll should be locked (section visible but not complete)
  const [isScrollLocked, setIsScrollLocked] = useState(false);
  // Ref to the main section element
  const sectionRef = useRef<HTMLElement>(null);
  // Ref to the main scrollable container
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  // Ref to the sticky content panel
  const stickyPanelRef = useRef<HTMLDivElement>(null);

  // Access Lenis instance to stop/start smooth scrolling
  const lenis = useLenis();

  // --- Stop/Start Lenis based on scroll lock state ---
  useEffect(() => {
    if (!lenis) return;

    if (isScrollLocked) {
      lenis.stop();
    } else {
      lenis.start();
    }
  }, [isScrollLocked, lenis]);

  // --- Check if section is locked to top (fully in viewport) ---
  useEffect(() => {
    const section = sectionRef.current;
    if (!section) return;

    const checkIfLocked = () => {
      const rect = section.getBoundingClientRect();
      // Section is considered "locked" when its top is at or very near viewport top (within 5px tolerance)
      // and it's actually visible (not scrolled past)
      const isLocked = rect.top <= 5 && rect.top >= -5 && rect.bottom >= window.innerHeight;
      setIsFullyVisible(isLocked);
    };

    // Use scroll listener on window to check position
    window.addEventListener('scroll', checkIfLocked, { passive: true });
    // Initial check
    checkIfLocked();

    return () => {
      window.removeEventListener('scroll', checkIfLocked);
    };
  }, []);

  // --- Scroll Handler for internal container ---
  useEffect(() => {
    const container = scrollContainerRef.current;
    if (!container) return;

    const handleScroll = () => {
      const scrollableHeight = container.scrollHeight - window.innerHeight;
      const stepHeight = scrollableHeight / slidesData.length;
      const newActiveIndex = Math.min(
        slidesData.length - 1,
        Math.floor(container.scrollTop / stepHeight)
      );
      setActiveIndex(newActiveIndex);

      // Check if internal scroll has reached the end (last slide fully reached)
      // Add a small buffer (10px) to account for scroll precision
      const isAtEnd = container.scrollTop >= scrollableHeight - 10;
      setHasCompletedInternalScroll(isAtEnd);
    };

    container.addEventListener('scroll', handleScroll);
    // Initial check
    handleScroll();
    return () => container.removeEventListener('scroll', handleScroll);
  }, []);

  // --- SCROLL LOCK: Force user to complete internal scroll before proceeding ---
  useEffect(() => {
    const container = scrollContainerRef.current;
    const section = sectionRef.current;
    if (!container || !section) return;

    // Calculate the scroll position where section should lock
    const getSectionLockPosition = () => {
      const rect = section.getBoundingClientRect();
      return window.scrollY + rect.top;
    };

    const handleWheel = (e: WheelEvent) => {
      const rect = section.getBoundingClientRect();
      const scrollableHeight = container.scrollHeight - window.innerHeight;
      const isAtEnd = container.scrollTop >= scrollableHeight - 10;
      const isAtStart = container.scrollTop <= 5;

      // Different states of the section visibility
      const isLockedAtTop = rect.top <= 10 && rect.top >= -10;
      const isApproachingFromAbove = rect.top > 10 && rect.top < window.innerHeight;
      const isVisible = rect.top < window.innerHeight && rect.bottom > 0;

      // Update scroll lock state
      const shouldLock = isVisible && !(isAtEnd && e.deltaY > 0) && !(isAtStart && e.deltaY < 0);
      setIsScrollLocked(shouldLock);

      // SCROLLING DOWN
      if (e.deltaY > 0) {
        // Section is approaching - snap it to top and start internal scroll
        if (isApproachingFromAbove && !isAtEnd) {
          e.preventDefault();
          e.stopPropagation();
          // Snap to lock position
          const lockPos = getSectionLockPosition();
          window.scrollTo({ top: lockPos, behavior: 'auto' });
          // Apply scroll to internal container
          container.scrollTop += Math.abs(e.deltaY);
          return;
        }

        // Section is locked - handle internal scroll
        if (isLockedAtTop && !isAtEnd) {
          e.preventDefault();
          e.stopPropagation();
          container.scrollTop += e.deltaY;
          return;
        }

        // Section is visible but not at top (maybe scrolled past) - snap back
        if (isVisible && !isLockedAtTop && !isAtEnd && rect.top < 0) {
          e.preventDefault();
          e.stopPropagation();
          const lockPos = getSectionLockPosition();
          window.scrollTo({ top: lockPos, behavior: 'auto' });
          container.scrollTop += Math.abs(e.deltaY);
          return;
        }
      }

      // SCROLLING UP
      if (e.deltaY < 0) {
        // Section is locked - handle internal scroll if not at start
        if (isLockedAtTop && !isAtStart) {
          e.preventDefault();
          e.stopPropagation();
          container.scrollTop += e.deltaY;
          return;
        }

        // Section is visible but scrolled past - snap back and handle internal scroll
        if (isVisible && !isLockedAtTop && !isAtStart && rect.top < 0) {
          e.preventDefault();
          e.stopPropagation();
          const lockPos = getSectionLockPosition();
          window.scrollTo({ top: lockPos, behavior: 'auto' });
          container.scrollTop += e.deltaY;
          return;
        }
      }

      // At boundaries - unlock and allow normal scroll
      if ((isAtEnd && e.deltaY > 0) || (isAtStart && e.deltaY < 0)) {
        setIsScrollLocked(false);
      }
    };

    // Handle touch events for mobile
    let touchStartY = 0;
    const handleTouchStart = (e: TouchEvent) => {
      touchStartY = e.touches[0].clientY;
    };

    const handleTouchMove = (e: TouchEvent) => {
      const touchY = e.touches[0].clientY;
      const deltaY = touchStartY - touchY;
      touchStartY = touchY;

      const rect = section.getBoundingClientRect();
      const scrollableHeight = container.scrollHeight - window.innerHeight;
      const isAtEnd = container.scrollTop >= scrollableHeight - 10;
      const isAtStart = container.scrollTop <= 5;
      const isLockedAtTop = rect.top <= 10 && rect.top >= -10;
      const isApproachingFromAbove = rect.top > 10 && rect.top < window.innerHeight;
      const isVisible = rect.top < window.innerHeight && rect.bottom > 0;

      // Apply same logic as wheel
      if (deltaY > 0 && isVisible && !isAtEnd) {
        e.preventDefault();
        if (isApproachingFromAbove || !isLockedAtTop) {
          const lockPos = getSectionLockPosition();
          window.scrollTo({ top: lockPos, behavior: 'auto' });
        }
        container.scrollTop += Math.abs(deltaY);
        return;
      }

      if (deltaY < 0 && isLockedAtTop && !isAtStart) {
        e.preventDefault();
        container.scrollTop += deltaY;
        return;
      }
    };

    // Use capture phase to intercept before Lenis
    window.addEventListener('wheel', handleWheel, { passive: false, capture: true });
    window.addEventListener('touchstart', handleTouchStart, { passive: true });
    window.addEventListener('touchmove', handleTouchMove, { passive: false });

    return () => {
      window.removeEventListener('wheel', handleWheel, { capture: true });
      window.removeEventListener('touchstart', handleTouchStart);
      window.removeEventListener('touchmove', handleTouchMove);
    };
  }, []);
  
  // Dynamic styles for the background and text color transitions
  const dynamicStyles = {
    backgroundColor: slidesData[activeIndex].bgColor,
    color: slidesData[activeIndex].textColor,
    transition: 'background-color 0.7s ease, color 0.7s ease',
  };

  // Styles for the grid pattern on the right side
  const gridPatternStyle: React.CSSProperties = {
    '--grid-color': 'rgba(0, 0, 0, 0.12)',
    backgroundImage: `
      linear-gradient(to right, var(--grid-color) 1px, transparent 1px),
      linear-gradient(to bottom, var(--grid-color) 1px, transparent 1px)
    `,
    backgroundSize: '3.5rem 3.5rem',
  };

  // Internal scroll should capture events when:
  // - Section is fully visible (locked to top)
  // - NOT at the very end of internal scroll (to allow exiting to next section)
  // Note: We allow capture at start so user can scroll INTO the section
  const shouldPreventLenis = isFullyVisible && !hasCompletedInternalScroll;

  return (
      <section
        ref={sectionRef}
        className="h-screen relative w-full sticky top-0 rounded-tr-2xl rounded-tl-2xl "
      >
      <div
        ref={scrollContainerRef}
        className={`h-full w-full ${isFullyVisible ? 'overflow-y-auto' : 'overflow-hidden'}`}
        style={{
          scrollbarWidth: 'none',
          msOverflowStyle: 'none',
          // Prevent scroll capture when section is not fully visible
          pointerEvents: isFullyVisible ? 'auto' : 'none'
        } as React.CSSProperties}
        // Only prevent Lenis when section is fully visible and internal scroll hasn't completed
        // When internal scroll reaches the end, remove prevention so user can scroll to next section
        {...(shouldPreventLenis ? { 'data-lenis-prevent': true } : {})}
      >
        <div style={{ height: `${slidesData.length * 100}vh` }}>
        <div ref={stickyPanelRef} className="sticky top-0 h-screen w-full flex flex-col items-center justify-center" style={dynamicStyles}>
          <div className="grid grid-cols-1 md:grid-cols-2 h-full w-full max-w-7xl mx-auto">
            
            {/* Left Column: Text Content, Pagination & Button */}
            <div className="relative flex flex-col justify-center p-8 md:p-16 border-r border-black/10">
              {/* Pagination Bars */}
              <div className="absolute top-16 left-16 flex space-x-2">
                {slidesData.map((_, index) => (
                  <button
                    key={index}
                    onClick={() => {
                        const container = scrollContainerRef.current;
                        if(container){
                            const scrollableHeight = container.scrollHeight - window.innerHeight;
                            const stepHeight = scrollableHeight / slidesData.length;
                            container.scrollTo({ top: stepHeight * index, behavior: 'smooth' });
                        }
                    }}
                    className={`h-1 rounded-full transition-all duration-500 ease-in-out ${
                      index === activeIndex ? 'w-12 bg-black/80' : 'w-6 bg-black/20'
                    }`}
                    aria-label={`Go to slide ${index + 1}`}
                  />
                ))}
              </div>
              
              <div className="relative h-64 w-full">
                {slidesData.map((slide, index) => (
                  <div
                    key={index}
                    className={`absolute inset-0 transition-all duration-700 ease-in-out ${
                      index === activeIndex
                        ? 'opacity-100 translate-y-0'
                        : 'opacity-0 translate-y-10'
                    }`}
                  >
                    <h2 className="text-5xl md:text-6xl font-bold tracking-tighter">{slide.title}</h2>
                    <p className="mt-6 text-lg md:text-xl max-w-md">{slide.description}</p>
                  </div>
                ))}
              </div>

              {/* Get Started Button */}
              <div className="absolute bottom-16 left-16">
                <a
                  href="#get-started"
                  className="px-10 py-4 bg-black text-white font-semibold rounded-full uppercase tracking-wider hover:bg-gray-800 transition-colors"
                >
                  Get Started
                </a>
              </div>
            </div>

            {/* Right Column: Image Content with Grid Background */}
            <div className="hidden md:flex items-center justify-center p-8" style={gridPatternStyle}>
              <div className="relative w-[50%] h-[80vh] rounded-2xl overflow-hidden shadow-2xl border-4 border-black/5">
                <div 
                  className="absolute top-0 left-0 w-full h-full transition-transform duration-700 ease-in-out"
                  style={{ transform: `translateY(-${activeIndex * 100}%)` }}
                >
                  {slidesData.map((slide, index) => (
                    <div key={index} className="w-full h-full">
                      <img
                        src={slide.image}
                        alt={slide.title}
                        className="h-full w-full object-cover"
                        onError={(e: SyntheticEvent<HTMLImageElement, Event>) => { 
                          const target = e.target as HTMLImageElement;
                          target.onerror = null; 
                          target.src = `https://placehold.co/800x1200/e2e8f0/4a5568?text=Image+Not+Found`; 
                        }}
                      />
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      </div>
    </section>
  );
}

export default ScrollingFeatureShowcase;
